.container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 20px 0;
}

.logo {
    width: 150px;
    object-fit: contain;
}

.navList {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding-left: 40px;
}

.mainLinks,
.functionalLinks {
    display: flex;
    gap: 0 20px;
}
